import React, { Component } from 'react'
import './App.css'
import Header from './header'
import { Spring } from 'react-spring'
import vsco from './img/Vsco.png'
import insta from './img/insta.png'
import twitter from './img/twitter.jpg'
import puj from './img/pujan.jpg'

class DigitalView extends Component {
	componentDidMount() {
		var getId = document.getElementById('aboutHeader')
		getId.style.color = '#84B1ED'
	}
	render() {
		return (
			<div>
				<Header />

				<div className="divWrapper">
					<div class="pujanInfo">
						<Spring from={{ opacity: 0 }} to={{ opacity: 1 }}>
							{(props) => (
								<div style={props}>
									<div className="artistStatementText">
										<img className="pujanPhoto" src={puj} />
										<p>
											Hi there! My name is Pujan Tandukar.
											<br />
											<br />
											I was born in Kathmandu, Nepal and I spent my entire childhood there before
											moving to Colorado. I am curretly a computer science student studying at
											University of Colorado Boulder. I shoot multiple genres within photography
											but my favorite one is portrait. I love meeting people and getting to know
											them. <br />
											If you want to share a photo experience with me, feel free to contact me!
										</p>
										<div className="iconsForAbout">
											<a href="https://vsco.co/pujan-" target="_blank">
												<img className="aboutIcons" src={vsco} />
											</a>
											<a href="https://www.instagram.com/puj.jpeg/" target="_blank">
												<img className="aboutIcons" src={insta} />
											</a>
											<a href="https://twitter.com/pujantandukar" target="_blank">
												<img className="aboutIcons" src={twitter} />
											</a>
										</div>
									</div>
								</div>
							)}
						</Spring>
					</div>
				</div>
			</div>
		)
	}
}

export default DigitalView
