import React, { Component } from 'react'
import './App.css'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import FilmView from './load'
import DigitalView from './digital'
import AboutView from './about'
import ArtistView from './statement'

class App extends Component {
	render() {
		return (
			<Router>
				<div className="App">
					<Route exact path="/film" component={FilmView} />
					<Route exact path="/" component={DigitalView} />
					<Route exact path="/about" component={AboutView} />
					<Route exact path="/artist-statement" component={ArtistView} />
				</div>
			</Router>
		)
	}
}

export default App
